.box-2-snaithe-main-info {
  position: relative;
  width: 100%;
  height: 50vh;
  flex-shrink: 0;
  background: #d9d9d9;
  display: flex;
  justify-content: space-between;
  transition: max-height 0.8s ease-in-out;
  overflow: hidden;
}

.image-container-right-1-3-custom {
  width: calc(50% - 235px);
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 90px 100px 90px 0;
  margin-left: 100px;
}

.text-container-3-custom {
  flex-grow: 1;
  padding: 0 80px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
}

.title-3-custom {
  color: #000;
  font-family: "Poppins", sans-serif;
  font-size: 30px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  margin-bottom: 1%;
  margin-top: 15%;
  margin-left: 70px;
}

.box3-second-titles-3-custom {
  color: #9a0000;
  font-family: "Poppins", sans-serif;
  font-size: 20px;
  margin-left: 70px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  text-decoration-line: underline;
  margin-bottom: 5px;
  transition: color 0.3s ease;
}

.box3-second-titles-3-custom:hover {
  color: #330202;
}

.text-3-custom1 {
  color: #000;
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  width: 90%;
  margin-left: 75px;
}

@media screen and (max-width: 1450px) {
  .box-2-snaithe-main-info {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    height: auto;
  }

  .image-container-right-1-3-custom {
    width: 100%;
    margin: 20px 0;
    margin-left: 0;
    margin-bottom: -15%;
    order: 2;
  }

  .text-container-3-custom {
    width: 100%;
    margin-left: 2%;
    padding: 20px;
    margin-top: 2%;
    margin-bottom: -7%;
    text-align: center;
    align-items: center;
  }

  .text-3-custom {
    width: 78%;
  }

  .box-1-text1 {
    width: 90%;
  }

  .toggle-button-2,
  .close-button-2 {
    position: static;
    margin-top: 5%;
    margin-bottom: 8%;
    margin-left: 3%;
  }
}

@media screen and (max-width: 1000px) {
  .text-container-3-custom {
    width: 100%;
    margin-left: -6%;
    margin-top: -6%;
    padding: 20px;
    margin-bottom: -7%;
    text-align: center;
    align-items: center;
  }
}

@media screen and (max-width: 750px) {
  .text-container-3-custom {
    width: 100%;
    margin-left: -12%;
    margin-top: -13%;
    padding: 20px;
    margin-bottom: -7%;
    text-align: center;
    align-items: center;
  }
  .title-3-custom {
    font-size: 25px;
  }

  .box-second-titles {
    font-size: 19px;
  }

  .box-1-text {
    font-size: 14px;
  }

  .toggle-button-2,
  .close-button-2 {
    position: static;
    margin-top: 5%;
    margin-bottom: 8%;
    margin-left: 12%;
  }
}

@media screen and (max-width: 1450px) {
  .box {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    height: auto;
  }
  .title-3-custom {
    font-size: 25px;
  }

  .box-second-titles {
    font-size: 19px;
  }

  .box-1-text {
    font-size: 14px;
  }
}
