.box-container {
  background-image: url("../../../assets/backimage1.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  height: 100vh;
  overflow: hidden;
  position: relative;
}

.bid-type-box {
  border: 1px solid #b49662;
  background: #000;
  width: 20%;
  height: 600px;
  flex-shrink: 0;
  margin: 0 2%;
  position: relative;
  transform: translateY(0);
  transition: transform 0.5s ease-in-out;
}
.bid-type-box2 {
  border: 1px solid #b49662;
  background: #f0f8ff;
  width: 20%;
  height: 600px;
  flex-shrink: 0;
  margin: 0 2%;
  position: relative;
  transform: translateY(0);
  transition: transform 0.5s ease-in-out;
}

.bid-type-box img {
  width: 100%;
  height: 50%;
  object-fit: cover;
}

.bid-type-box2 img {
  width: 100%;
  height: 50%;
  object-fit: cover;
}

.bid-type-box .box-content {
  padding: 20px;
  box-sizing: border-box;
  height: 50%;
  text-align: left;
  display: flex;
  margin-left: -5%;
  margin-top: -5%;
  flex-direction: column;
  justify-content: space-between;
}

.bid-type-box .luxury-title {
  color: #b49662;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 30px;
  margin-left: -15%;
  text-align: left;
}

.bid-type-box .bidtype-box-text {
  color: #b49662;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  margin-left: 5%;
}

.bid-type-box .bid-link:hover {
  -webkit-text-fill-color: #74b2f7;
}

.bid-type-box .bid-link {
  color: #74b2f7;
  -webkit-text-fill-color: #74b2f7;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  margin-left: 60%;
  text-decoration: underline;
  align-items: end;
  text-align: end;
  width: 40%;
  margin-bottom: -3%;
}

.bid-type-box2 .box-content2 {
  padding: 20px;
  box-sizing: border-box;
  height: 50%;
  text-align: left;
  display: flex;
  margin-left: -5%;
  margin-top: 0%;
  flex-direction: column;
  justify-content: space-between;
}

.bid-type-box2 .luxury-title2 {
  color: #000;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 30px;
  margin-left: 5%;
  text-align: left;
}

.bid-type-box2 .bidtype-box-text2 {
  color: #000;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  margin-left: 5%;
}

.bid-type-box2 .bid-link2:hover {
  -webkit-text-fill-color: #330202;
}

.bid-type-box2 .bid-link2 {
  color: #9a0000;
  -webkit-text-fill-color: #9a0000;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  margin-left: 60%;
  text-decoration: underline;
  align-items: end;
  text-align: end;
  font-size: 15px;
  margin-bottom: 2%;
  width: 40%;
}

.box-1-animate,
.box-3-animate {
  transform: translateY(-50%);
  transition: transform 0.5s ease-in-out;
}

.scroll-animated .box-1-animate,
.scroll-animated .box-3-animate {
  transform: translateY(0);
}

.box-2-animate,
.box-4-animate {
  transform: translateY(50%);
  transition: transform 0.5s ease-in-out;
}

.scroll-animated .box-2-animate,
.scroll-animated .box-4-animate {
  transform: translateY(0);
}
.email-input-box {
  color: #b49662;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: 0 2% 2%;
  margin-left: -380%;
  margin-top: 30%;
  padding: 10px;
  flex-shrink: 0;
  border-radius: 11px;
  background: rgba(180, 150, 98, 0.7);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 40px;
  text-align: left;
  position: relative;
  animation: none;
}

.email-title {
  font-size: 18px;
  width: 100%;
  margin-bottom: 0%;
  text-align: left;
}

.email-input {
  color: #b49662 !important;
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  background-color: transparent !important;
  line-height: normal;
  width: auto;
  padding: 10px;
  margin-bottom: 20%;
  margin-top: 5%;
  margin-left: 2%;
  border: none;
  border-bottom: 1px solid #422e0a;
  outline: none;
  -webkit-text-fill-color: #422e0a;
  opacity: 100;
  caret-color: #422e0a;
}

.thank-you {
  font-size: 15px;
  margin: 0;
  -webkit-text-fill-color: #422e0a;
  font-weight: 500;
  margin-left: 2%;
  margin-top: 10%;
  margin-bottom: 23%;
  cursor: pointer;
  opacity: 0;
  animation: fadeInOut 2s ease-in-out forwards;
}

@keyframes fadeInOut {
  0%,
  100% {
    opacity: 0;
  }
  20%,
  80% {
    opacity: 1;
  }
}

.email-submit-button {
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0;
  margin-left: 10px;
}

.email-submit-button svg {
  opacity: 1;
  margin-right: -100px;
  background: none;
}
