body {
  margin: 0;
  padding: 0;
}

.footer {
  bottom: 0;
  left: 0;
  width: 100%;
  height: 350px;
  background: #010f1e;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}
.line1 {
  width: 120%;
  height: 1px;
  background-color: #03203f;
  position: absolute;
  margin-left: -2%;
  margin-top: 11%;
}
.logo-left-footer {
  max-width: 300px;
  margin-top: -55px;
  margin-left: 50px;
  position: relative;
  overflow: hidden;
  transition: transform 0.8s ease-in-out;
}

.logo-left-footer:hover {
  transform: scale(1.2);
}

.logo-left-footer:hover img {
  cursor: pointer;
}

.center-section {
  position: absolute;
  left: 90%;
  transform: translate(-50%, 50%);
  display: flex;
  align-items: center;
  line-height: normal;
  margin-bottom: -250px;
}

.icons-tooltip {
  display: flex;
}

.icon-link {
  position: relative;
  display: block;
}

.icon-name {
  visibility: hidden;
  position: absolute;
  top: -50px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #b49662;
  color: #010f1e;
  padding: 4px 8px;
  border-radius: 6px;
  white-space: nowrap;
  font-size: 12px;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s ease-in-out;
}

.icon-link:hover .icon-name {
  visibility: visible;
  opacity: 1;
}

.icon-socials {
  margin: 10px;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}

.icon-socials:hover {
  transform: scale(1.8);
}

#icon-ig {
  margin: 10px;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}

#icon-ig:hover svg path:nth-child(2) {
  fill: #b49662;
}

#icon-ig svg path:nth-child(2) {
  fill: #b49662;
}

#icon-ig:hover {
  transform: scale(1.8);
  fill: #91e5f6;
}

.icon-socials:hover svg path:nth-child(1) {
  fill: #b49662;
}
.icon-socials svg path:nth-child(1) {
  fill: #b49662;
}
.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #032a53;
  width: 150px;
  height: auto;
  padding: 5px;
  z-index: 1;
  border-radius: 7px;
}

.dropdown-content ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.dropdown-content li {
  padding: 8px 12px;
  cursor: pointer;
}

.dropdown-content li a {
  text-decoration: none;
  color: #b49662;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown-content {
  top: -380%;
  -webkit-text-fill-color: #91e5f6;
  opacity: 60%;
  font-weight: medium;
  font-size: 16px;
}
.dropdown-content li a:hover {
  text-decoration: none;
  color: #352304;
}

.bottom-text {
  color: #b49662;
  font-size: 12px;
  font-weight: 400;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 50%);
  display: flex;
  align-items: center;
  line-height: normal;
  margin-bottom: -265px;
}

.right-section {
  text-align: center;
  margin-top: 40px;
}

#right-text {
  color: #b49662;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 300;
  text-align: left;
  line-height: normal;
  margin-top: -200px;
  margin-right: -200px;
  display: inline-block;
  vertical-align: top;
}

#rightone-text2 {
  color: #b49662;
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  margin-bottom: -20px;
  margin-top: 2px;
}

#rightone-text {
  color: #b49662;
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  text-align: left;
  line-height: normal;
  display: inline-block;
  margin-top: -50px;
  vertical-align: top;
  margin-right: 490px;
}

textarea {
  border: none;
  resize: vertical;
  outline: none;
  padding: 10px;
  font-weight: 300;
  font-size: 15px;
  box-sizing: border-box;
  width: 100%;
  height: 150px;
  border: 0.3px solid #91e5f6;
}

.titles {
  color: #91e5f6;
  text-decoration: none;
}

.titles:hover {
  color: #91e5f6;
}

a {
  color: #b49662;
  text-decoration: none;
  transition: color 0.3s ease;
}

a:hover {
  color: #d6c09b;
}

