.slider-container {
  position: relative;
  overflow: hidden;
  width: 100%;
  max-width: 100%;
  height: auto;
}

.slider-images {
  display: flex;
  transition: transform 0.4s ease;
}

.slider-image {
  flex-shrink: 0;
  width: 100%;
  height: auto;
  object-fit: cover;
}

.slider-navigation {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  position: absolute;
  bottom: 10px;
  width: 100%;
}

.slider-circle {
  cursor: pointer;
  width: 12px;
  height: 11px;
  margin: 0 5px;
  opacity: 0.6;
  transition: opacity 0.3s ease;
}

.slider-circle.active {
  opacity: 1;
}

body {
  overflow-x: hidden;
  margin: 0;
}

::-webkit-scrollbar {
  width: 10px;
  cursor: pointer;
}

::-webkit-scrollbar-track {
  background: #3a2402;
  cursor: pointer;
}

::-webkit-scrollbar-thumb {
  background-color: #b49662;
  border-radius: 15px;
  cursor: pointer;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #d6c09b;
  border-radius: 15px;
  cursor: pointer;
}
