.logo-bidtype {
  flex-shrink: 0;
  transition: transform 0.8s ease-in-out;
}

.logo-bidtype:hover {
  transform: scale(1.1);
  transition: color 0.8s ease-in-out, transform 0.8s ease-in-out;
  cursor: pointer;
}

.logo-bidtype1 {
  display: flex;
  width: 60px;
  margin-left: 20%;
  height: auto;
  justify-content: center;
  margin-top: 0px;
}

.three-name-header {
  display: flex;
  margin-left: 8%;
  justify-content: center;
}

.Buy-1,
.Bid-1,
.Sell-1,
.Buy-2,
.Bid-2,
.Sell-2 {
  display: flex;
  font-size: 22px;
  align-items: center;
  margin: 0 52%;
}

.icons {
  display: flex;
  gap: 10px;
}

.icon-menu-header {
  position: relative;
  margin-right: 20px;
}

.flag-icon {
  position: relative;
  margin-top: 3px;
  margin-right: 50px;
}

.icon-menu-header svg {
  width: 24px;
  height: 24px;
  fill: #ffffff;
  transition: fill 0.3s ease;
}

.icon-search-header svg {
  width: 18px;
  height: 18px;
  margin-top: 3px;
  margin-right: 20px;
  transition: fill 0.3s ease;
  cursor: pointer;
}

.icon-menu-header:hover svg,
.icon-menu-header.clicked svg {
  fill: #79a8df;
  cursor: pointer;
}

.dropdown-menu-header {
  display: none;
  position: absolute;
  top: calc(100% + 14.4px);
  right: -690%;
  width: 238px;
  height: 480px;
  flex-shrink: 0;
  background: #010f1e;
  opacity: 85%;
  padding: 20px;
  box-sizing: border-box;
  transition: opacity 0.3s ease, transform 0.3s ease;
  transform: translateY(-10px);
}

.dropdown-menu-header.visible {
  display: block;
  transform: translateY(0);
}

.dropdown-menu-header ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.dropdown-menu-header li {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  text-transform: capitalize;
  color: #b49662;
  margin-bottom: 15px;
  position: relative;
}

.dropdown-menu-header li a {
  color: #79a8df;
  text-decoration: none;
}

.dropdown-menu-header li:hover::before,
.dropdown-menu-header li:hover,
.dropdown-menu-header li:hover a,
.dropdown-menu-header li:active::before,
.dropdown-menu-header li:active,
.dropdown-menu-header li:active a {
  color: #79a8df;
  cursor: pointer;
}

.dropdown-menu-header li::before {
  content: "";
  position: absolute;
  width: 238px;
  height: 32px;
  background: #062f4b;
  top: -4px;
  left: -20px;
  opacity: 0;
  z-index: -1;
  transition: opacity 0.3s ease;
}

.dropdown-menu-header li:hover::before,
.dropdown-menu-header li:active::before {
  opacity: 1;
}

