.flag-option {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  cursor: pointer;
  padding-left: 5px;
  padding-top: 5px;
  position: relative;
}

.flag-label {
  flex: 1;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  color: #b49662;
  margin-left: 20px;
  margin-top: 3px;
}

.flag-icon svg {
  cursor: pointer;
  width: 24px;
  height: 16px;
  fill: #ffffff;
  margin-right: 10px;
}

.flag-dropdown svg {
  margin-right: 25px;
  margin-left: -20px;
}

.flag-option:hover .flag-label,
.flag-option:hover svg path,
.flag-option:hover .flag-icon svg path,
.flag-option:active .flag-label,
.flag-option:active svg path,
.flag-option:active .flag-icon svg path {
  color: #79a8df;
}

.flag-option:hover .flag-label::before,
.flag-option:hover svg path::before,
.flag-option:hover .flag-icon svg path::before,
.flag-option:active .flag-label::before,
.flag-option:active svg path::before,
.flag-option:active .flag-icon svg path::before {
  opacity: 1;
}

.flag-label::before,
.flag-icon svg path::before {
  content: "";
  position: absolute;
  height: 110%;
  background: #062f4b;
  top: 3px;
  right: 0%;
  z-index: -1;
  opacity: 0;
  width: 238px;
  transition: opacity 0.3s ease;
}

.flag-label:hover::before,
.flag-option:hover svg path::before,
.flag-option:active .flag-label::before,
.flag-option:active svg path::before,
.flag-option:hover .flag-icon svg path::before,
.flag-option:active .flag-icon svg path::before {
  opacity: 1;
  width: 238px;
}

