.bid-category-drop-container {
  width: 100%;
  height: 150px;
  flex-shrink: 0;
  background: #042c49;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
}

.bid-category-search-bar {
  display: flex;
  height: 34px;
  padding: 0px 4px;
  align-items: center;
  gap: 155px;
  flex-shrink: 0;
  border: 0.5px solid #b49662;
  width: 10%;
}

.bid-category-search-bar-left {
  color: #b49662;
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.bid-drop-category-container {
  position: relative;
  cursor: pointer;
  margin-bottom: -50px;
}

.bid-drop-category-text {
  color: #b49662;
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  padding: 4px;
  width: 110%;
  border: 0.5px solid #b49662;
}

.bid-category-dropdown-container {
  position: absolute;
  width: 110%;
  max-width: 110%;
  top: 100%;
  left: 0;
  background: #fafafa;
  border: 1px solid #b49662;
  display: none;
  z-index: 999;
  margin-top: 5px;
}

.bid-drop-category-container.active .bid-category-dropdown-container {
  display: block;
}

.bid-category-dropdown {
  position: absolute;
  width: 115%;
  top: 100%;
  left: 0;
  background: #fafafa;
  border: 1px solid #b49662;
  z-index: 999;
  margin-top: 5px;
}

.bid-category-dropdown-item {
  width: 100%;
  height: auto;
  fill: #010f1e;
  cursor: pointer;
  padding: 8px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
}

.bid-category-dropdown-item:hover {
  background-color: #010f1e;
  -webkit-text-fill-color: #b49662;
}

.bid-category-download-action {
  align-items: center;
  -webkit-text-fill-color: #b49662;
  cursor: pointer;
}
