/*Box 1 to 8 */
.bid-page-main-box-container {
  background-image: none;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  height: auto;
  position: relative;
  margin-bottom: 54%;
}

.bid-page-main-box {
  border: 1px solid #b49662;
  background: #000;
  width: 20%;
  height: 600px;
  flex-shrink: 0;
  margin-left: 2%;
  margin-right: 2%;
  position: relative;
}

.bid-page-main-box img {
  width: 100%;
  height: 50%;
  object-fit: cover;
}

.bid-page-main-box .bid-page-main-box-content {
  padding: 20px;
  box-sizing: border-box;
  height: 50%;
  text-align: left;
  display: flex;
  margin-left: -5%;
  margin-top: -5%;
  flex-direction: column;
  justify-content: space-between;
}

.bid-page-main-box .bid-page-main-luxury-title {
  color: #b49662;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 30px;
  margin-left: -15%;
  text-align: left;
}

.bid-page-main-box .bid-page-main-box-text {
  color: #b49662;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  margin-left: 5%;
}

.bid-page-main-box .bid-page-main-link:hover {
  -webkit-text-fill-color: #74b2f7;
}

.bid-page-main-box .bid-page-main-link {
  color: #74b2f7;
  -webkit-text-fill-color: #74b2f7;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  margin-left: 60%;
  text-decoration: underline;
  align-items: end;
  text-align: end;
  width: 40%;
  margin-bottom: -3%;
}

.margin-box {
  width: 100%;
  margin: 1% 0;
}

/*Box 9 */
.bid-page-main-box-9 {
  display: flex;
  align-items: flex-start;
  margin: 5% 0;
}

.bid-page-main-box-9 img {
  width: 250%;
  height: auto;
  margin-left: -390%;
  margin-top: 20%;
  object-fit: cover;
}

.bid-page-main-box-content-right-box-9 {
  padding: 20px;
  box-sizing: border-box;
  width: 100%;
  margin-top: -120%;
  margin-left: -80%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.bid-page-main-luxury-title-box-9 {
  -webkit-text-fill-color: #000;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 30px;
  margin-left: 0;
  text-align: left;
}

.bid-page-main-box-text-box-9 {
  color: #000;
  font-family: "Poppins", sans-serif;
  font-weight: 275;
  font-size: 18px;
  width: 150%;
  margin-left: 0;
}

.bid-page-main-starting-bid-box-9 {
  color: #ff0000;
  text-decoration-line: underline;
}

.bid-page-main-starting-bid-box-9:hover {
  -webkit-text-fill-color: #201f1f;
  text-decoration-line: underline;
}

/* Grid View List */
.bid-main-page-grid-view path {
  transition: fill 0.3s ease;
}

.bid-main-page-grid-view path:hover {
  fill: #9a0000 !important;
}

.svg-container-bid-page-grid-view {
  display: flex;
  justify-content: flex-end;
  margin-right: 0.5%;
  margin-top: 0.3%;
}
