.box-5-snaithe-main-info {
    position: relative;
    width: 100%;
    height: 50vh;
    flex-shrink: 0;
    background: linear-gradient(180deg, #000 0%, #000 45.22%, #000 64.24%, #030303 77.99%, #040404 100%);    display: flex;
    justify-content: space-between;
    transition: max-height 0.8s ease-in-out;
    overflow: hidden;
  }
  
  .image-container-right-1-5-custom {
    width: calc(40% - 235px);
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 90px 100px 70px 0;
    margin-left: 100px;
  }
  
  .text-container-5-custom {
    flex-grow: 1;
    padding: 0 80px;
    position: relative;
    display: flex;
    margin-left: 5%;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
  }
  
  .title-5-custom {
    color: #B49662;
    font-family: "Poppins", sans-serif;
    font-size: 30px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    margin-bottom: 1%;
    margin-top: 15%;
  }
  
  .box3-second-titles-5-custom {
    color: #9a0000;
    font-family: "Poppins", sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    width: 100%;
    text-decoration-line: underline;
    margin-bottom: 5px;
    transition: color 0.3s ease;
  }
  
  .box3-second-titles-5-custom:hover {
    color: #af1212;
  }
  
  .text-5-custom1 {
    color: #B49662;
    font-family: "Poppins", sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    width: 70%;
  }
  
  @media screen and (max-width: 1450px) {
    .box-5-snaithe-main-info {
      display: flex;
      flex-direction: column-reverse;
      align-items: center;
      height: auto;
    }
  
    .image-container-right-1-5-custom {
      width: 100%;
      margin: 20px 0;
      margin-left: 0;
      margin-bottom: -15%;
      order: 2;
    }
  
    .text-container-5-custom {
      width: 100%;
      margin-left: 2%;
      padding: 20px;
      margin-top: 2%;
      margin-bottom: -7%;
      text-align: center;
      align-items: center;
    }
  
    .text-5-custom {
      width: 78%;
    }
  
    .box-1-text1 {
      width: 90%;
    }
  
    .toggle-button-2,
    .close-button-2 {
      position: static;
      margin-top: 5%;
      margin-bottom: 8%;
      margin-left: 5%;
    }
  }
  
  @media screen and (max-width: 1000px) {
    .text-container-5-custom {
      width: 100%;
      margin-left: -6%;
      margin-top: -6%;
      padding: 20px;
      margin-bottom: -7%;
      text-align: center;
      align-items: center;
    }
  }
  
  @media screen and (max-width: 750px) {
    .text-container-5-custom {
      width: 100%;
      margin-left: -12%;
      margin-top: -13%;
      padding: 20px;
      margin-bottom: -7%;
      text-align: center;
      align-items: center;
    }
    .title-5-custom {
      font-size: 25px;
    }
  
    .box-second-titles {
      font-size: 19px;
    }
  
    .box-1-text {
      font-size: 14px;
    }
  
    .toggle-button-2,
    .close-button-2 {
      position: static;
      margin-top: 5%;
      margin-bottom: 8%;
      margin-left: 12%;
    }
  }
  
  @media screen and (max-width: 1450px) {
    .box {
      display: flex;
      flex-direction: column-reverse;
      align-items: center;
      height: auto;
    }
    .title-5-custom {
      font-size: 25px;
    }
  
    .box-second-titles {
      font-size: 19px;
    }
  
    .box-1-text {
      font-size: 14px;
    }
  }
  