.box {
  position: relative;
  width: 100%;
  height: 50vh;
  flex-shrink: 0;
  background: #d9d9d9;
  display: flex;
  justify-content: space-between;
  transition: max-height 0.8s ease-in-out;
  overflow: hidden;
}

.image-container-right-1 {
  width: calc(50% - 300px);
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 90px 100px 90px 0;
  margin-left: -900px;
}

.text-container {
  flex-grow: 1;
  padding: 0 80px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
}

.image-container-right-1 img {
  max-width: 160%;
  max-height: 160%;
  transition: transform 0.3s ease-in-out;
}

.title {
  color: #000;
  font-family: "Poppins", sans-serif;
  font-size: 30px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  margin-bottom: 1%;
  margin-top: 6%;
  margin-left: 70px;
}

.box-second-titles {
  color: #9a0000;
  font-family: "Poppins", sans-serif;
  font-size: 20px;
  margin-left: 70px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  text-decoration-line: underline;
  margin-bottom: 5px;
  transition: color 0.3s ease;
}

.box-second-titles:hover {
  color: #330202;
}

.box-1-text {
  color: #000;
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  width: 38%;
  margin-left: 70px;
}

@media screen and (max-width: 1450px) {
  .box {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    height: auto;
  }

  .image-container-right-1 {
    width: 100%; 
    margin: 20px 0;
    margin-left: 0;
    order: 2; 
  }

  .text-container {
    width: 100%;
    margin-left: -12%;
    padding: 20px;
    margin-top: -5%;
    text-align: center;
    align-items: center;
  }

  .text-container1 {
    width: 100%;
    margin-left: 22%;
    padding: 20px;
    margin-right: 15%;
  }
  .box-1-text {
    width: 78%;
  }

  .box-1-text1 {
    width: 90%;
  }

  .toggle-button-1,
  .close-button-1 {
    position: static;
    margin-top: 5%;
    margin-left: 17%;
  }
}
@media screen and (max-width: 550px) {
  .title {
    font-size: 25px;
  }

  .box-second-titles {
    font-size: 19px;
  }

  .box-1-text {
    font-size: 14px;
  }
}
